<template>
    <div id="app" ref="app">
        <button @click="SetFullscreen(true)" 
                ref="fullscreenBtn" 
                id="fullscreenBtn">Fullscreen</button>
        <div v-show="true"
             id="container"
             class="fixed">
            <canvas id="unity-canvas"
                    class="fixed"
                    ref="uc">
            </canvas>
            <div id="videoContainer"
                 class="fixed"
                 ref="vc">
            </div>
        </div>

        <div v-if="ShowLoading"
             id="loaderMain"
             class="fixed">
            <div id="loadingIndicator">
                <div class="loader"></div>
            </div>
        </div>

        <div v-if="ShowScanning"
             id="scanningMain"
             class="fixed">
            <div id="loadingIndicator">
                <div class="loader"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as AR_Renderer from "@/AR-Renderer/ar_renderer";
    import { SceneLoader } from "@/AR-Renderer/scene_loader"

    export default {
        name: 'App',
        data() {
            return {
                isFullscreen: false,
                sceneLoader: null,
                ShowLoading: false,
                ShowScanning: false,
            };
        },
        created() {
            addEventListener('beforeunload', () => {
                fetch("/quit").catch((err) => {
                    throw err;
                });
            });
        },
        mounted() {
            this.Initialization();
        },
        methods: {
            async ReloadScene() {
                this.sceneLoader.ReloadScene();
            },
            async Initialization() {
                let unityContainer = this.$refs.uc;
                let videoContainer = this.$refs.vc;

                this.sceneLoader = new SceneLoader(unityContainer, videoContainer, this);

                let vue = this;
                window.addEventListener("orientationchange", function () {
                    vue.ReloadScene();
                }, false);

                AR_Renderer.Init(unityContainer, () => {
                    setTimeout(() => {
                        if (this.$refs.fullscreenBtn) {
                            this.$refs.fullscreenBtn.remove();
                        }
                    }, 10500);
                });
            },

            SetFullscreen(state) {
                this.$refs.fullscreenBtn.remove();
                if (state) {
                    this.$refs.app.requestFullscreen()
                        .then(() => {
                            this.isFullscreen = true;
                        })
                        .catch((err) => {
                            console.error(err);
                        });
                } else {
                    document.exitFullscreen()
                        .then(() => {
                            this.isFullscreen = false;
                        })
                        .catch((err) => {
                            console.error(err);
                        })
                }
            },

            ShowLoadingIndicator(state) {
                this.ShowLoading = state;
            },
            ShowScanningIndicator(state) {
                this.ShowScanning = state;
            },
        }
    }
</script>

<style>
    @import '../css/styles.css';
</style>
